<template>
    <div class="bonus-account">
        <div class="header-block">
            <p class="cabinet-header">{{$localizeFilter('bonus_account') + ' ' + coin}}</p>
            <p class="cabinet-text">{{$localizeFilter('bonus_account_text')}}</p>
        </div>
        <BonusBalance class="balance" :type="'balance'" :coin="coin" @fillBalance="fillBalance" @withdrawFromBalance="withdrawFromBalance" @showWithdrawalRequests="showWithdrawalRequests"/>
        <BonusOperations class="operations" :type="'bonusAccount'" :coin="coin" :showOptions="true"/>
        <!-- <Modal v-if="showWithdrawalModal" :type="'bonusAccount'" :coin="coin" :actionType="actionType" :step="actionStep" @updateModal="updateModal" @closeModal="closeModal" @showWithdrawalConfirmation="showWithdrawalConfirmation"/> -->
        <BonusWithdrawalModal v-if="showWithdrawalModal"  :type="'bonusAccount'"  :coin="coin" @closeModal="closeModal" @showWithdrawalConfirmation="showWithdrawalConfirmation"/>
        <BonusWithdrawalConfirmationModal v-if="showWithdrawalConfirmationModal"  @closeModal="closeConfirmationModal" :withdrawId="withdrawId" :commission="commission" :coinTicker="coin" />
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import BonusBalance from '@/components/BonusBalance.vue'
import BonusOperations from '@/components/BonusOperations.vue'
// import Modal from "@/components/modals/Modal.vue"
import BonusWithdrawalConfirmationModal from "@/components/newModals/BonusWithdrawalConfirmationModal.vue"
import BonusWithdrawalModal from '../components/newModals/BonusWithdrawalModal.vue'
// import LinkTelegramWindow from '@/components/LinkTelegramWindow.vue'

export default defineComponent({
    props: {
        coin: {
            type: String,
            required: true
        },
        withdraw: {
            type: Boolean,
            default: false
        }
    },
    components: {
        BonusBalance,
        BonusOperations,
        // Modal,
        BonusWithdrawalConfirmationModal,
        BonusWithdrawalModal
        // LinkTelegramWindow
    },
    setup() {
        
    },
    data: () => ({
      showWithdrawalModal: false,
    //   actionType: 'withdrawal', // replenish, withdrawal
    //   actionStep: 1,
      withdrawId: null,
      commission: 0,
      showWithdrawalConfirmationModal: false
    }),
    methods: {
        showWithdrawalConfirmation(withdrawBalanceRequestId, fee) {
            this.closeModal()
            this.withdrawId = withdrawBalanceRequestId
            this.commission = fee || 0
            setTimeout(()=>{
                this.showWithdrawalConfirmationModal = true
            })
        },
        closeModal() {
            this.showWithdrawalModal = false
            this.clearAll()
        },
        closeConfirmationModal() {
            this.showWithdrawalConfirmationModal = false
            this.closeModal()
        },
        clearAll() {
            // this.actionStep = 1
            const path = this.$router?.currentRoute?.value?.fullPath
            if (path) {
                const newRoute = path.replace(`&withdraw=1`, '')
                this.$router.push(newRoute)
            }
        },
        fillBalance() {
            // this.actionType = 'replenish'
            // this.actionStep = 1
            // this.showWithdrawalModal = true
        },
        withdrawFromBalance() {
            // this.actionType = 'withdrawal'
            this.showWithdrawalModal = true
        },
        showWithdrawalRequests() {
            this.$router.push(`/cabinet/withdrawalRequests?from=bonusAccount&coin=${this.coin}`)
        },
        // updateModal(step) {
        //     this.actionStep=step       
        // } 
    },
    mounted() {
        if (this.withdraw) {
            this.$nextTick(()=>{
                this.showWithdrawalModal = true
            })
        }
    }
})
</script>

